@media (max-width: 480px) {
  .blogCard {
    grid-template-columns: 50% 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      'img img'
      'title title'
      'description description'
      'badges badges';
    grid-auto-rows: minmax(0, 1fr);
  }

  .codeBlock {
    font-size: 16px !important;
  }
}

